<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for edit Area  -->
      <v-btn
        class="ma-2"
        outlined
        small
        fab
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-icon>mdi-pencil-outline</v-icon>
      </v-btn>
      <!-- end  build button for add Area  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Edit Area</span>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name Ar*"
                  required
                  v-model="area.name.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name En*"
                  required
                  v-model="area.name.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name Tr*"
                  required
                  v-model="area.name.tr"
                ></v-text-field>
              </v-col>
              <!--  -->
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
          <v-btn color="secondary" text @click="updateArea">
            <span v-if="!progressEditArea">Update Area</span>
            <v-progress-circular
              v-if="progressEditArea"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { areaController } from "./area";
export default {
  props: ["areaData"],
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],

      // image
      file: null,
      image: null,
      area: this.areaData,

      //
      progressEditArea: false,
      isUpdating: false,
      dialog: false,
    };
  },

  methods: {
    updateArea: async function () {
      if (this.$refs.form.validate()) {
        this.progressEditArea = true;
        var state = await areaController.updateArea(this.area);
        if (state) {
          this.dialog = false;
          this.restData();
          this.$emit("getVal");
        } else {
          this.dialog = false;
        }
        this.progressEditArea = false;
      }
    },

    restData() {
      this.image = null;
      this.file = null;
      this.progressEditArea = false;
    },
  },
};
</script>
 















